import React from "react"
import {Link} from "gatsby"
import Img from "gatsby-image";
import {useTranslation} from "react-i18next";
import ButtonBack from "../../components/elements/BackButton";


export default function PartnerSingle({post}) {
    const {t} = useTranslation();
    return (
        <div className="main-wrapper pt-36 pb-20 min-h-screen">
            <div className="md:container mx-auto mt-4">
                <div className="flex px-4 md:px-0">
                    <Link to="/about-us/">
                        <ButtonBack text={t('return')}/>
                    </Link>
                </div>
                <div className="grid grid-cols-1 mt-10">
                    <h1 className="text-3xl lg:text-4xl text-white text-center font-bold">
                        {post.title}
                    </h1>
                </div>
                <div className="flex justify-center">
                    <div className="w-full sm:w-3/4 lg:w-2/4 mt-16 px-4 lg:px-0 flex flex-col lg:block">
                        <Img objectFit="contain" className="h-full w-full text-center flex lg:block mx-auto lg:mx-0 lg:float-left lg:mr-6" fixed={post.featuredImage.node.localFile.childImageSharp.fixed} alt="Partner image"/>
                        <div className="text-white text-justify" dangerouslySetInnerHTML={{__html: post.content}}/>
                    </div>
                </div>
            </div>

        </div>
    )
}
