import React from "react"
import {Link} from "gatsby"
import Img from "gatsby-image";
import {useTranslation} from "react-i18next";
import ButtonBack from "../../components/elements/BackButton";


export default function BlogSingle({post}) {
    const {t} = useTranslation();

    return (
        <div className="main-wrapper min-h-screen pt-24 lg:pt-36 pb-20">
            <div className="md:container mx-auto px-10 md:px-0 lg:mt-18">
                <span className="flex">
                    <Link to="/blogs/" className="flex">
                        <ButtonBack text={t('return')}/>
                    </Link>
                </span>
                <h1 className="text-3xl md:text-5xl md:text-center text-white font-bold mt-4">
                    {post.title}
                </h1>
                <p className="relative mt-16">
                    <Img style={{ float: 'right', aspectRatio: '1/1' }} className="w-full md:w-1/3 mb-8 md:ml-8" fluid={post.featuredImage.node.localFile.childImageSharp.fluid} alt="Blog image"/>
                    <p className="text-white text-justify" dangerouslySetInnerHTML={{__html: post.content}}/>
                </p>
            </div>
        </div>
    )
}
