import React from "react"
import {Link} from "gatsby"
import Img from "gatsby-image"
import {useTranslation} from "react-i18next";
import ButtonBack from "../../components/elements/BackButton";

export default function ProjectSingle({post}) {
    const {t} = useTranslation();

    function capitalizeFirstLetter(str) {
        return str[0].toUpperCase() + str.slice(1);
    }

    return (
        <div className="main-wrapper" style={{paddingTop: '60px'}}>
            <div className="md:container mx-auto px-4 md:px-0">
                <span className="flex">
                    <Link to="/portfolio/"  className="flex mt-12">
                        <ButtonBack text={t('return')}/>
                    </Link>
                </span>
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 mt-10">
                    <div className="lg:col-span-2 flex flex-col justify-between lg:-mr-16 z-10">
                        <h1 className="text-4xl text-white font-bold md:text-center md:pt-28">
                            {post.title}
                        </h1>
                        <div className="pt-4 pb-2 flex flex-row md:flex-row-reverse">
                            {post.tags.nodes.map((tag, i) => (
                                <span className="text-center text-white text-xs mr-5" key={i}>
                                    <img src={`/images/develop_stack/${tag.name}.svg`} style={{height: "30px", margin: "10px auto"}} alt={tag.name} title={tag.name}/>
                                    {capitalizeFirstLetter(tag.name)}
                                </span>
                            ))}
                        </div>
                    </div>
                    <div className="lg:col-span-3 pt-12 lg:mt-0">
                        <Img className="object-fill h-full w-full" fluid={post.featuredImage.node.localFile.childImageSharp.fluid} alt={"Project image"}/>
                    </div>
                </div>

                <div className="flex justify-center pb-16">
                    <p className="mt-16 text-white lg:w-2/4 px-4 lg:px-0" dangerouslySetInnerHTML={{__html: post.content}} />
                </div>
            </div>
        </div>
    )
}
