import "../scss/global.scss";
import '../i18n/i18n';

import React from "react"
import {graphql} from "gatsby"
import BlogSingle from "./types/blogs_single";
import ProjectSingle from "./types/projects_single";
import PartnerSingle from "./types/partners_single";
import Navigation from "../components/layout/Navigation";
import {SEO} from "../components/seo";

export default function BlogPost({data}) {
    const post = data.allWpPost.nodes[0];

    return (
        <>
            {post.categories.nodes[0].name === 'blogs' &&
            <>
                <Navigation isPost={true} pageNr={'04'} post={post}/>
                <BlogSingle post={post}/>
            </>
            }
            {post.categories.nodes[0].name === 'projects' &&
            <>
                <Navigation isPost={true} pageNr={'03'} post={post}/>
                <ProjectSingle post={post}/>
            </>
            }
            {post.categories.nodes[0].name === 'partners' &&
            <>
                <Navigation isPost={true} pageNr={'02'} post={post}/>
                <PartnerSingle post={post}/>
            </>
            }
        </>
    )
}
export const Head = ({data}) => <SEO title={data.allWpPost.nodes[0].title} description={''}/>

export const query = graphql`
  query($slug: String!) {
    allWpPost(filter: { slug: { eq: $slug } }) {
      nodes {
        categories {
            nodes {
             name
            }
        }
        excerpt
        title
        content
        slug
        tags {
              nodes {
                name
              }
            }
        featuredImage {
              node {
                sourceUrl
                 localFile {
                   publicURL
                   childImageSharp {
                     gatsbyImageData(
                        placeholder: BLURRED
                     )
                     fluid(maxWidth: 2000, quality: 100) {
                         ...GatsbyImageSharpFluid
                     }
                     fixed(width: 200, quality: 100) {
                         ...GatsbyImageSharpFixed
                     }
                   }
                 }
              }
            }
      }
    }
  }
`
